.search-box__wrapper {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding-top: 2rem;
}

.search-box__field {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-right: 1rem;
  border-radius: 10px;
  font-family: 'Zilla Slab', serif;
  font-size: 1.2rem;
  padding: 0 0.5em;
  outline: none;
}

.search-box__field:focus {
  -webkit-box-shadow: 0 0 10px 2px skyblue;
          box-shadow: 0 0 10px 2px skyblue;
}

.search-box__button {
  padding: 0.5em 1em;
  font-family: 'Zilla Slab', serif;

  background-color: #fc624d;
  border: #18587a solid 1px;
  border-radius: 25px;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: #18587a;
  -webkit-box-shadow: 0 0 5px 1px black;
          box-shadow: 0 0 5px 1px black;
  outline: none;
  cursor: pointer;

  -webkit-transition: all linear 200ms;

  -o-transition: all linear 200ms;

  transition: all linear 200ms;
}

.search-box__button.lifted {
  -webkit-box-shadow: 0 0 15px 1px black;
          box-shadow: 0 0 15px 1px black;
  -webkit-transform: translate(-2px, -2px);
      -ms-transform: translate(-2px, -2px);
          transform: translate(-2px, -2px);
}



